/* Login.css - Estilos para la pantalla de login */

/* Estilos básicos */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

/* Contenedor principal */
.min-h-screen {
  min-height: 100vh;
}

.bg-gray-100 {
  background-color: #f3f4f6;
}

.flex {
  display: flex;
}

.items-center {
  align-items: center;
}

.justify-center {
  justify-content: center;
}

/* Tarjeta de login */
.max-w-md {
  max-width: 28rem;
}

.w-full {
  width: 100%;
}

.p-8 {
  padding: 2rem;
}

.bg-white {
  background-color: #ffffff;
}

.rounded-lg {
  border-radius: 0.5rem;
}

.shadow-lg {
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

/* Encabezado */
.text-center {
  text-align: center;
}

.mb-8 {
  margin-bottom: 2rem;
}

.mb-4 {
  margin-bottom: 1rem;
}

.inline-flex {
  display: inline-flex;
}

.w-16 {
  width: 4rem;
}

.h-16 {
  height: 4rem;
}

.rounded-full {
  border-radius: 9999px;
}

.bg-green-100 {
  background-color: #d1fae5;
}

.text-green-700 {
  color: #047857;
}

.text-2xl {
  font-size: 1.5rem;
}

.font-bold {
  font-weight: 700;
}

.text-gray-800 {
  color: #1f2937;
}

.text-sm {
  font-size: 0.875rem;
}

.text-gray-600 {
  color: #4b5563;
}

.mt-2 {
  margin-top: 0.5rem;
}

/* Mensajes de error */
.mb-6 {
  margin-bottom: 1.5rem;
}

.p-3 {
  padding: 0.75rem;
}

.bg-red-50 {
  background-color: #fef2f2;
}

.border {
  border-width: 1px;
}

.border-red-200 {
  border-color: #fecaca;
}

.rounded-md {
  border-radius: 0.375rem;
}

.text-red-700 {
  color: #b91c1c;
}

.mr-2 {
  margin-right: 0.5rem;
}

/* Formulario */
.space-y-6 > * + * {
  margin-top: 1.5rem;
}

.block {
  display: block;
}

.font-medium {
  font-weight: 500;
}

.text-gray-700 {
  color: #374151;
}

.mb-1 {
  margin-bottom: 0.25rem;
}

.relative {
  position: relative;
}

.absolute {
  position: absolute;
}

.inset-y-0 {
  top: 0;
  bottom: 0;
}

.left-0 {
  left: 0;
}

.pl-3 {
  padding-left: 0.75rem;
}

.pointer-events-none {
  pointer-events: none;
}

.text-gray-400 {
  color: #9ca3af;
}

.pl-10 {
  padding-left: 2.5rem;
}

.pr-3 {
  padding-right: 0.75rem;
}

.py-2 {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}

.border-gray-300 {
  border-color: #d1d5db;
}

.shadow-sm {
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
}

input:focus {
  outline: none;
  border-color: #10b981;
  box-shadow: 0 0 0 3px rgba(16, 185, 129, 0.1);
}

/* Texto pequeño */
.mt-1 {
  margin-top: 0.25rem;
}

.text-xs {
  font-size: 0.75rem;
}

.text-gray-500 {
  color: #6b7280;
}

/* Checkbox y link */
.justify-between {
  justify-content: space-between;
}

.h-4 {
  height: 1rem;
}

.w-4 {
  width: 1rem;
}

.text-green-600 {
  color: #059669;
}

.ml-2 {
  margin-left: 0.5rem;
}

.text-green-600 {
  color: #059669;
}

a.text-green-600:hover {
  color: #047857;
}

/* Botón */
.py-2 {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}

.px-4 {
  padding-left: 1rem;
  padding-right: 1rem;
}

.border-transparent {
  border-color: transparent;
}

.text-white {
  color: #ffffff;
}

.bg-green-700 {
  background-color: #047857;
}

.bg-green-700:hover {
  background-color: #065f46;
}

.focus\:outline-none:focus {
  outline: none;
}

.focus\:ring-2:focus {
  box-shadow: 0 0 0 2px rgba(4, 120, 87, 0.5);
}

.focus\:ring-offset-2:focus {
  box-shadow: 0 0 0 2px white, 0 0 0 4px rgba(4, 120, 87, 0.5);
}

.focus\:ring-green-500:focus {
  box-shadow: 0 0 0 3px rgba(16, 185, 129, 0.5);
}

.opacity-70 {
  opacity: 0.7;
}

.cursor-not-allowed {
  cursor: not-allowed;
}

/* Footer */
.mt-6 {
  margin-top: 1.5rem;
}

.inset-0 {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.border-t {
  border-top-width: 1px;
}

.px-2 {
  padding-left: 0.5rem;
  padding-right: 0.5rem;
}

/* Ajustes para dispositivos móviles */
@media (max-width: 640px) {
  .p-8 {
    padding: 1.5rem;
  }
  
  .w-16, .h-16 {
    width: 3rem;
    height: 3rem;
  }
  
  .text-2xl {
    font-size: 1.25rem;
  }
  
  .flex.items-center.justify-between {
    flex-direction: column;
    align-items: flex-start;
  }
  
  .flex.items-center.justify-between > div:nth-child(2) {
    margin-top: 0.5rem;
  }
}